.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

body {
  background: rgb(28, 28, 28);
}

body .css-1tspw4m-modal {
  margin: 140px auto;
  padding: 24px;
}

body .css-1qsqxf2-modal {
  margin: 140px auto;
  padding: 24px;
}

.css-1t7vcue-chainBadge {
  background: rgb(28, 28, 28) !important;
}
/* button */
.MuiButton-root:disabled {
  background: #252f3a !important;
  opacity: 1 !important;
}
.MuiButton-root:disabled .MuiTypography-root {
  color: #ffffff80 !important;
}
.MuiButton-root {
  background: #76c8ff !important;
}
.MuiButton-root .MuiTypography-root {
  color: #000000d9 !important;
}

.ant-spin-dot-item {
  background-color: #68ffd8 !important;
}

.sui .ant-spin-dot-item {
  background-color: #76c8ff !important;
}

.MuiScopedCssBaseline-root {
  background: none !important;
  margin-top: 80px;
}

.body-container-bg {
  position: absolute;
  background: url("../src/images/sui-img/img-bg@2x.png") center no-repeat !important;
  background-size: 1200px 600px !important;
  position: absolute !important;
  left: 50% !important;
  top: 0px !important;
  transform: translateX(-50%) !important;
  height: calc(100vh - 82px) !important;
  padding: 0 !important;
  min-width: 1240px !important;
  z-index: -1 !important;
}

.body-container-bg-aptos {
  background: url("../src/images/default-img/img-bg-aptos@2x.png") center
    no-repeat !important;
  background-size: 1200px 600px !important;
}

/* .MuiCollapse-wrapperInner > div {
  background: #202020 !important;
  color     : #FF6868 !important;
} */
#wormhole-connect {
  padding-top: 80px;
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: auto;
  right: 0;
  height: 82px;
  /* padding   : 20px; */
  background: rgb(28, 28, 28);
  z-index: 1000;
}

.h5-select-modal {
  display: none;
}

.headerAll {
  max-width: 1400px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 40px;
  box-sizing: border-box;
}
.top-left-header {
  display: flex;
  align-items: center;
}

.left-logo {
  width: 132px;
  height: 32px;
  background: url("../src/images/default-img/img-logo@2x.png");
  background-size: 100% 100%;
}
.left-bridge-logo {
  display: none;
}

.left-toothpick {
  width: 66px;
  height: 24px;
  /* background     : url("../src/images/default-img/label-Alpha@2x.png"); */
  background: url("../src/images/default-img/img-Beta@2x.png");
  margin-left: 10px;
  background-size: 100% 100%;
}

.sui-header .left-toothpick {
  background: url("../src/images/sui-img/beta.png");
  background-size: 100% 100%;
}

.menu {
  display: flex;
  align-items: center;
}

.menu a {
  /* color             : #CDCDCD; */
  color: #a9bdb5;
  box-sizing: inherit;
  /* height         : 40px; */
  /* width          : 90px; */
  padding: 10px 20px;
  font-size: 14px;
  /* line-height    : 40px; */
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  font-weight: 600;
}
.sui-header .menu a {
  color: #7f98a7;
}

.menu a:hover,
.more_icon:hover {
  color: #fff !important;
}

.bridge {
  color: #fff !important;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* background     : url("../src/images/default-img/img-sel@2x.png"); */
  /* background-size: 100% 100%; */
}
.bridge .start-item {
  width: 22px;
  height: 22px;
  background: url("../src/images/default-img/icon-star-Aptos.png");
  background-size: 100% 100%;
}

.sui-header .bridge .start-item {
  background: url("../src/images/sui-img/icon-star2.png") !important;
  background-size: 100% 100% !important;
}

.select {
  /* width             : 244px; */
  /* height         : 36px; */
  margin-left: 10px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  /* justify-content   : space-between; */
}

.select > a {
  height: 32px;
  color: #fff;
  box-sizing: inherit;
  border: 1px solid #265b4f;
  text-decoration: none;
  cursor: pointer;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 8px;
}

.sui-header .select > a {
  border: 1px solid #025077;
}

.select > a img {
  width: 20px;
  height: 20px;
}

.spot {
  min-height: 34px !important;
  min-width: 34px !important;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 7px;
  box-sizing: border-box;
  border: 1px solid #265b4f !important;
  cursor: pointer;
}

.sui-header .spot {
  border: 1px solid #025077 !important;
}

.select .spot > span {
  display: block;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #fff;
}

.exhibit {
  display: none;
  position: absolute;
  right: 0px;
  top: 36px;
  width: 140px;
  background: #161616;
  font-size: 14px !important;
  padding: 8px 0;
}

.exhibit a {
  width: 140px;
  /* display     : block; */
  box-sizing: border-box;
  display: flex;
  align-items: center;
  color: #fff;
  padding: 6px 16px;
  margin-bottom: 8px;
  white-space: nowrap;
  text-decoration: none;
}

.exhibit a:hover {
  /* color: #68FFD8; */
  color: #fff;
}

.sui-header .exhibit a:hover {
  /* color: #76C8FF; */
  color: #fff;
}

.spot:hover .exhibit {
  display: block;
}
.more_icon {
  color: #a9bdb5;
  font-weight: 600;
  font-size: 14px;
  line-height: 14px;
  cursor: pointer;
  position: relative;
  padding: 10px 20px;
}
.sui-header .more_icon {
  color: #7f98a7;
}

.more-list {
  /* color: #A9BDB5; */
  position: absolute;
  right: 20px;
  top: 34px;
  width: 168px;
  background: #191919;
  border: 1px solid #213f37;
  padding: 0 16px 20px;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  box-sizing: border-box;
  display: none;
}

.earn-list {
  /* color: #A9BDB5; */
  position: absolute;
  left: 20px;
  top: 34px;
  width: 168px;
  background: #191919;
  border: 1px solid #213f37;
  padding: 0 16px 20px;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  box-sizing: border-box;
  display: none;
}

.sui-header .more-list .earn-list {
  border: 1px solid #013854;
}

.more_link {
  display: flex;
  align-items: center;
}
.more_link img {
  width: 16px !important;
  height: 16px !important;
  margin-right: 12px;
}
.more_link .hover-img {
  display: none;
}
.more_link:hover .hover-img {
  display: block;
}
.more_link:hover .default-img {
  display: none;
}
.more_link_beta {
  position: relative;
}
.more_link_beta .limit_beta {
  width: 28px !important;
  height: 12px !important;
  margin-right: 0;
  position: absolute;
  right: 50px;
  top: -6px;
}
.not_show {
  display: none !important;
}

.item-icon > svg {
  margin-right: 12px;
  width: 16px;
  height: 16px;
}
.item-icon-active svg {
  fill: #fff !important;
}
.more-list .item-icon > svg {
  width: 20px;
  height: 20px;
}
.more-list .ido-icon > svg {
  margin-left: 2px;
  width: 16px;
  height: 16px;
}
.bot_icon {
  height: 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.bot_icon svg {
  margin-right: 0;
  width: 18px;
  height: 18px;
}
.bot_icon a {
  margin: 0 !important;
}
.more-list a {
  margin-top: 20px;
  padding: 0;
}
.earn-list a {
  margin-top: 20px;
  padding: 0;
}
.line {
  height: 1px;
  width: 100%;
  background: #333;
  margin: 20px 0;
}
.more_icon:hover .icon {
  /* transform: rotate(180deg) !important; */
  /* animation: exchange 3s 1 linear !important; */
  transform: rotate(180deg) !important;
  color: #fff;
}
.more_icon:hover .earn-list {
  display: flex;
}
.more_icon:hover .more-list {
  display: flex;
}
.more_icon .icon {
  margin-left: 2px;
  transition: all 0.5s;
}

.beta-tag {
  position: absolute;
  top: -2px;
  right: -8px;
  width: 30px;
  height: 14px;
  z-index: 1;
}

.beta-tag-h5 {
  position: absolute;
  top: 0px;
  left: 48px;
  width: 24px;
  height: 12px;
  z-index: 1;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
@media (max-width: 1100px) {
  .headerAll {
    padding: 20px 20px;
  }
  .top-logo {
    min-width: 32px !important;
    margin-right: 0 !important;
  }
  .left-logo {
    width: 32px;
    height: 32px;
    background: url("../src/images/default-img/bitdge-h5-logo.png");
    background-size: 100% 100%;
  }
}

@media (max-width: 750px) {
  /* #wormhole-connect {
    padding-top: 200px;
  } */

  .header {
    height: auto;
    /* height: 200px; */
  }

  .headerAll {
    height: 88px;
    align-items: flex-start;
    padding: 0 12px;
  }
  .headerAll p .left-logo,
