  }

  .body-container-bg {
    width: 100% !important;
    height: 100% !important;
    /* min-width   : 100% !important; */
    background: url("../src/images/sui-img/img-bg@2x.png") center no-repeat !important;
    background-size: 375px 220px !important;
    padding: 0 !important;
    padding-bottom: 80px !important;
