    transform: translateX(-100%); */
  }

  .spot {
    display: none;
  }

  .menu-list {
    position: initial;
    width: 100%;
    height: auto;
    background: none;
    align-items: center;
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    width: 100%;
    padding: 0 !important;
  }
  .menu-list a,
  .h5_more_icon {
    height: 25px;
    position: inherit;
    transform: translateX(0%);
    /* margin: 0 auto; */
    box-sizing: content-box;
    margin: 10px auto 4px;
    font-size: 16px;
    padding: 10px 20px;
    width: calc(100% - 40px);
    font-family: none !important;
    color: #a9bdb5;
    font-weight: 600;
  }
  .h5_earn_icon {
    position: inherit;
    transform: translateX(0%);
    /* margin: 0 auto; */
    box-sizing: content-box;
    margin: 10px auto 4px;
    font-size: 16px;
    padding: 10px 20px;
    width: calc(100% - 40px);
    font-family: none !important;
    color: #a9bdb5;
    font-weight: 600;
  }
  .sui-h5_earn_icon {
    color: #7f98a7;
  }
  .sui-header .menu-list a,
  .sui-header .h5_more_icon {
    color: #7f98a7;
  }
  .footer-box {
    height: 140px;
    width: 100%;
    position: absolute;
    bottom: 56px;
    margin: 0;
    display: block;
  }
  .footer-box > a {
    display: flex !important;
    position: initial;
    width: calc(100% - 58px);
    margin: 0 auto;
    justify-content: flex-start;
    transform: translateX(0);
  }
  .footer-box > a span {
    height: 14px;
    line-height: 14px;
    margin-left: 8px;
    color: #a9bdb5;
  }
  .sui-header .footer-box > a span {
    color: #7f98a7;
  }
  .footer-box .exhibit {
    position: initial;
    width: calc(100% - 40px);
    height: auto;
    background: none;
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
    margin: 0 auto;
    padding: 18px 0;
  }
  .footer-box .exhibit .item-icon > svg {
    width: 24px;
    height: 24px;
    margin: 0 18px 0 4px;
    color: #a9bdb5;
  }
  .sui-header .footer-box .exhibit .item-icon > svg {
    color: #7f98a7;
  }
  .footer-box .exhibit a {
    margin: 0;
    width: auto;
    position: initial;
    transform: translateX(0);
    padding: 0;
  }
  .top-logo {
    margin: 30px 0 0 0 !important;
  }
  .h5_more_icon {
    color: #a9bdb5;
  }
  .sui-header .h5_more_icon {
    color: #7f98a7;
  }
  /* .h5_more-list{
    color: #a9bdb5;
  } */

  /* .h5_more_icon:hover .h5_more-list{
    display: block;
  } */
  .h5_more_icon .icon {
    transition: all 0.5s;
    margin-left: 2px;
  }
  .h5_more_icon .icon > svg {
    width: 12px;
    height: 12px;
  }

  .h5_earn_icon .icon {
    transition: all 0.5s;
    margin-left: 2px;
  }
  .h5_earn_icon .icon > svg {
    width: 12px;
    height: 12px;
  }

  /* .h5_more_icon:hover .icon{
    transform: rotate(180deg) !important;
  } */
  /* .h5_more-list{
    display: none;
  } */
  .h5_more-list .line {
    margin: 12px 0 20px;
  }
  .h5_more-list a {
    margin: 20px 0 4px;
    width: 100%;
    padding: 0;
    font-size: 14px;
  }
  .h5_earn-list .line {
    margin: 12px 0 20px;
  }
  .h5_earn-list a {
    margin: 20px 0 4px;
    width: 100%;
    padding: 0;
    font-size: 14px;
  }
  .more_link_beta .limit_beta {
    width: 28px !important;
    height: 12px !important;
    margin-right: 0;
    position: absolute;
    right: -24px;
    top: -3px;
  }
}
@media (max-height: 670px) {
  .menu-list {
    overflow-y: scroll;
    height: 326px;
  }
}
